import { defHttp } from '/@/utils/http/axios';

enum Api {
  //顶端指标
  getTopInfoUrl = '/da/station-operation/overview/topInfo',

  //电站信息
  getRunDataUrl = '/da/station-operation/overview/runData',

  //发电统计
  getPowerStatUrl = '/da/station-operation/overview/powerStat',

  //社会贡献
  getSocialConStatUrl = '/da/station-operation/overview/socialConStat',

  //实时功率
  getActivePowerUrl = '/da/station-operation/overview/activePower',

  //损失分布
  getLossElecStatUrl = '/da/station-operation/overview/lossElecStat',

  //电量趋势
  getElecTrendUrl = '/da/station-operation/overview/elecTrend',

  //发电效率
  getPowerEfficiencyUrl = '/da/station-operation/overview/powerEfficiency',
}

export const getTopInfoData = (params) => defHttp.post({ url: Api.getTopInfoUrl, params }, {});

export const getRunDataData = (params) => defHttp.post({ url: Api.getRunDataUrl, params }, {});

export const getPowerStatData = (params) => defHttp.post({ url: Api.getPowerStatUrl, params }, {});

export const getSocialConStatData = (params) =>
  defHttp.post({ url: Api.getSocialConStatUrl, params }, {});

export const getActivePowerData = (params) =>
  defHttp.get({ url: Api.getActivePowerUrl, params }, {});

export const getLossElecStatData = (params) =>
  defHttp.post({ url: Api.getLossElecStatUrl, params }, {});

export const getElecTrendData = (params) => defHttp.get({ url: Api.getElecTrendUrl, params }, {});

export const getPowerEfficiency = (params) =>
  defHttp.get({ url: Api.getPowerEfficiencyUrl, params }, {});
